import InputInterface from './inputInterface';

export default async function ValidateInput(root: HTMLElement) : Promise<boolean> {
    const inputs = root.querySelectorAll("ks-input-text, ks-input-textarea, ks-input-check, ks-input-number, ks-input-select");
    const dynamicinputs = root.querySelectorAll("ks-input-dynamic");
    let valid = true;
    
    for (let i = 0; i < inputs.length; i++) {
        const input = (inputs[i] as unknown) as InputInterface;

        await input.Validate();
        if(await input.IsValid() == false) valid = false;
    }

    for (let i = 0; i < dynamicinputs.length; i++) {
        const input = (dynamicinputs[i] as unknown) as InputInterface;
        
        await input.Validate();
        if(await input.IsValid() == false) valid = false;
    }

    return valid;
}